<template>
	<div class="Incomebottom-box">
		<div class="Incomebottom-btn">
		    <el-button v-if="nextShow" @click="textingInfo()" type="primary" size="medium"></el-button>
			<el-button v-if="submitShow" @click="sucSubmit()" type="primary" size="medium"></el-button>
		</div>
	</div>
</template>

<script>
	export default {
		name: "IncomeBottom",
		data() {
			return {
					submitShow:false,
					nextShow:true
			}
		},
		methods: {
			textingInfo(){
				this.submitShow=true;
				this.nextShow=false;
				this.$bus.$emit("changes", true);
			},
			sucSubmit(){
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });          
        })
      
			}
		}
	}
</script>

<style lang="stylus" scoped>
	.Incomebottom-box {
		width: 100%;
		text-align: center;
		position: fixed;
		bottom:0;
		background-color: #FFFFFF;
	}
	.Incomebottom-box .Incomebottom-btn {
		padding: .1rem 0 .3rem;
	}
	.el-button--medium{
		width: 2rem;
	}
</style>



