<script>
	export default {
		name: "IncomeProve",
		data() {
			return {
				approvalTitle: "",
				activeNames: [1,2,3,4,5],
				showToolbar: true,
				popupVisible: false,
				popupVisible2: false,
				popupVisible3: false,
				popupVisible4: false,
				popupVisible5: false,
				popupVisible6: false,
				popupVisible7: false,
				input20: '',
				input21: '',
				input22: '',
				input23: '',
				input24: '',
				input25: '',
				input26: '',
				input27: '',
				input30: '',
				input31: '',
				input33: '',
				input34: '',
				input50: '',
				input61:'',
				input62:'',
				upList2: [],
				textarea2: '',
				workflowShow: false,
				message: "",
				slots: [{
					values: [
						{
							proveType: "",
						},
						{
							proveType: "",
						},
						{
							proveType: "",
						}
					],
				}],
				useSlots: [{
					values: ['', '', '', '', '', '','','','','','','','',''],
				}],
				dataSlots: [{
					values: [
						{
							incomeType: "",
							isPayslip:"",
							receiveMode:""
						},
						{
							incomeType: "",
							isPayslip:"",
							receiveMode:""
						}
					],
				}],	
				dateSlots:[

					{
					  flex: 1,
					  values: ['2010','2011','2012','2013','2014','2015','2016', '2017', '2018', '2019', '2020'],
					  className: 'slot1',
					  textAlign: 'right'
					}, {
					  divider: true,
					  content: '-',
					  className: 'slot2'
					}, {
					  flex: 1,
					  values: ['01', '02', '03','04','05','06','07','08','09','10','11', '12'],
					  className: 'slot3',
					  textAlign: 'left'
					}
				],	
				restaurants1: [],
				state1: '',
			}
		},
		mounted() {
			const _this = this;
			this.$bus.$on('changes', function(msg) {
				if (msg == true) {
					_this.isShow();
					_this.activeNames = 4;
					console.log(msg);
				}
			});
			
			this.restaurants1 = this.loadAll1();
		},
		methods: {
			loadAll1() {
				return [{
						"value": "101",
						"lable": ""
					},
					{
						"value": "202",
						"lable": "",
					},
					{
						"value": "303",
						"lable": ""
					}
				];
			},
			querySearch1(queryString, cb) {
				var restaurants = this.restaurants1;
				var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
				//  callback 
				cb(results);
			},
			createFilter(queryString) {
				return (restaurant) => {
					return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0)||
						(restaurant.lable.toLowerCase().indexOf(queryString.toLowerCase()) === 0)
				};
			},	
			handleSelect1(item) {
				console.log(item);
			},
			handleIconClick1(ev) {
				console.log(ev);
			},
			handleChange(val) {
			},
			isShow() {
				this.workflowShow = true;
			},
			onValuesChange(picker, values) {
				if (values[0] > values[1]) {
					picker.setSlotValue(1, values[0].proveType);
				}
				this.input20 = values[0].proveType;
			},
			onValuesChange2(picker, values) {
				if (values[0] > values[1]) {
					picker.setSlotValue(1, values[0].incomeType);
				}
				this.input22 = values[0].incomeType;
			},
			onValuesChange3(picker, values) {
				if (values[0] > values[1]) {
					picker.setSlotValue(1, values[0].receiveMode);
				}
				this.input24 = values[0].receiveMode;
			},
			onValuesChange4(picker, values) {
				if (values[0] > values[1]) {
					picker.setSlotValue(1, values[0].isPayslip);
				}
				this.input26 = values[0].isPayslip;
			},
			onValuesChange5(picker, values) {
				this.input30 = values[0]+"-"+values[1];
			},
			onValuesChange6(picker, values) {
				this.input33 = values[0]+"-"+values[1];
			},
			onValuesChange7(picker, values) {
				if (values[0] > values[1]) {
					picker.setSlotValue(1, values[0]);
				}
				this.input62 = values[0];
			},
			handleRemove(file, fileList) {
				console.log(file, fileList);
			},
			handlePreview(file) {
				console.log(file);
			},
			handleExceed(files, fileList) {
				this.$message.warning(` 3  ${files.length}  ${files.length + fileList.length} `);
			},
			beforeRemove(file, fileList) {
				return this.$confirm(` ${ file.name }`);
			}
		}

	}
</script>

