<script>
	export default {
		name: "ProveMsg",
		data() {
			return {
				approvalTitle: "",
				activeNames: [1],
				showToolbar: true,
				message: "",
				popupVisible: false,
				popupVisible3: false,
				popupVisible6: false,
				workflowShow: false,
				input20: '',
				input21: '',
				input22:'',
				input23:'',
				input24:'',
				input25:'',
				input26:'',
				input27:'',
				input61:'',
				input62:'',
				upList2: [],
				textarea2: '',
				useSlots: [{
					values: ['', '', '', '', '', '','','','','','','','',''],
				}],
				dataSlots: [{
					values: [
						{
							proveType: "",						
							receiveMode:""
						},
						{
							proveType: "",							
							receiveMode:""
						}
					],
				}],
				restaurants1: [],
				restaurants2: [],
				state1: '',
				state2: '',
      }

		},
		mounted() {
			const _this = this;
			this.$bus.$on('changes', function(msg) {
				if (msg == true) {
					_this.isShow();
					_this.activeNames = 4;
				}
			});
			this.restaurants1 = this.loadAll1();
			this.restaurants2 = this.loadAll2();
		},
		methods: {
			loadAll1() {
				return [{
						"value": "12345678",
						"lable": "--"
					},
					{
						"value": "23456789",
						"lable": "--",
					},
					{
						"value": "34567890",
						"lable": "--"
					}
				];
			},
			loadAll2() {
				return [{
						"value": "101",
						"lable": ""
					},
					{
						"value": "202",
						"lable": "",
					},
					{
						"value": "303",
						"lable": ""
					}
				];
			},
			querySearch1(queryString, cb) {
				var restaurants = this.restaurants1;
				var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
				//  callback 
				cb(results);
			},
			querySearch2(queryString, cb) {
				var restaurants = this.restaurants2;
				var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
				//  callback 
				cb(results);
			},
			createFilter(queryString) {
				return (restaurant) => {
					return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0)||
						(restaurant.lable.toLowerCase().indexOf(queryString.toLowerCase()) === 0)
				};
			},	
			handleSelect1(item) {
				console.log(item);
			},
			handleSelect2(item) {
				console.log(item);
			},
			handleIconClick1(ev) {
				console.log(ev);
			},
			handleIconClick2(ev) {
				console.log(ev);
			},
			handleChange(val) {

			},
			ShouPup() {
				this.popupVisible = true

			},
			isShow() {
				this.workflowShow = true;
			},
			onValuesChange(picker, values) {
				if (values[0] > values[1]) {
					picker.setSlotValue(1, values[0].proveType);
				}
				this.input26 = values[0].proveType;
			},
			onValuesChange2(picker, values) {
				if (values[0] > values[1]) {
					picker.setSlotValue(1, values[0].receiveMode);
				}
				this.input27 = values[0].receiveMode;
			},
			onValuesChange6(picker, values) {
				if (values[0] > values[1]) {
					picker.setSlotValue(1, values[0]);
				}
				this.input62 = values[0];
			},
			sureSelect() {
				console.log(this.input20);
				this.input21 = this.input20;
				this.popupVisible = !this.popupVisible
			},
			errSelect() {
				this.popupVisible = !this.popupVisible
			},
			httpRequest(options){
				var that = this
				// 
				let file = options.file
				//
				if (file.type == 'image/jpeg' || file.type == 'image/png' || file.type == 'image/JPG') {
				 var  isJPG =  true
				} else {
				 isJPG =  false
				}
			 // 
				const isLt2M = file.size / 1024 / 1024 < 5
				if (!isJPG) {
					this.$message.error(' JPG/PNG/JPEG !')
				}

			 // HTML5FileReader
				var reader = new FileReader();
				//img
			 var  img = new Image();
				let filename = options.filename
				if (file) {
					reader.readAsDataURL(file)
				}
				if (isJPG && isLt2M) {
					reader.onload = (e) => {
					let base64Str = reader.result.split(',')[1]
					img.src = e.target.result
						// base64
					img.onload = function () {
							// canvasDOMcanvasbase64
							var canvas = document.createElement('canvas');
							var context = canvas.getContext('2d');
							// 
							var originWidth = this.width;
							var originHeight = this.height;
							// 
							var maxWidth = 300,
									maxHeight = 300;
							// 
							var targetWidth = originWidth,
									targetHeight = originHeight;
							// 
							if(originWidth > maxWidth || originHeight > maxHeight) {
									if(originWidth / originHeight > maxWidth / maxHeight) {
											// 
											targetWidth = maxWidth;
											targetHeight = Math.round(maxWidth * (originHeight / originWidth));
									} else {
											targetHeight = maxHeight;
											targetWidth = Math.round(maxHeight * (originWidth / originHeight));
									}
							}
							//
							canvas.width = targetWidth;
							canvas.height = targetHeight;
							// 
							context.clearRect(0, 0, targetWidth, targetHeight);
							// 
							context.drawImage(img, 0, 0, targetWidth, targetHeight);
							/*img*/
							//base64
							var newUrl = canvas.toDataURL('image/jpeg', 0.92);
							// console.log(newUrl);

							this.dialogImageUrl = newUrl;
// 										 that.Api.post("https://jsonplaceholder.typicode.com/posts/",{ fileContent:newUrl})
// 										 .then(res => {
// 											 
// 										 })
// 										 .catch(err => {
// 										 })


							//base64MB
							var str = newUrl.replace('data:image/png;base64,', '');
							var equalIndex = str.indexOf('=');
							if(str.indexOf('=')>0) {
									str=str.substring(0, equalIndex);
							}
							var strLength=str.length;
							var fileLength=parseInt(strLength-(strLength/8)*2);
							// MB
							var size = "";
							size = (fileLength/(1024 * 1024)).toFixed(2);
							var sizeStr = size + "";                        //
							var index = sizeStr.indexOf(".");                    //
							var dou = sizeStr.substr(index + 1 ,2)            //
							if(dou == "00"){                                //0000                
									return sizeStr.substring(0, index) + sizeStr.substr(index + 3, 2)
							}
							console.log(size);
							// console.log(newUrl);
						}
					}
				}
			},
			handleRemove(file, fileList) {
				console.log(file, fileList);
			},
			handlePreview(file) {
				console.log(file);
			},
			handleExceed(files, fileList) {
				this.$message.warning(` 3  ${files.length}  ${files.length + fileList.length} `);
			},
			beforeRemove(file, fileList) {
				return this.$confirm(` ${ file.name }`);
			}
		}

	}
</script>

